.horizontal-div
    background-image: url('./grid-02.png')
    background-repeat: repeat
    top: 0
    left: 0
    //height: 100vh
    width: 100vw
    z-index: 1

[data-scroll-section] *
    white-space: normal

.boxed
    padding: 1rem
    border: 1px solid #fff
    cursor: default
    background: rgba(255,255,255,0)
    color: #000000
    transition: background 0.4s $cub-bez, color 0.5s $cub-bez
    &.bold
        border-width: 2px
//&:hover
    background: rgba(255,255,255,1)
    color: #000

.container.cycles
    position: relative
    z-index: 2
    .title
        font-size: 20pt
        position: absolute
        line-height: 1em
        top: 40vh
        left: 25vw
        transform: translate(-50%, -50%)
        overflow: hidden
        color: #fff

    // Letter-by-letter animation
    .let
        display: inline-block
        transform: translateY(150%)
        .inner
            display: inline-block
            transition: transform 0.4s ease-in-out
    .title.is-inview .let .inner
        transform: translateY(-150%)
    @for $i from 1 through 6
        .let:nth-child(#{$i}) .inner
            transition-delay: 0.4s + 0.1s * $i
    @keyframes spin
        0%
            transform: rotate(0deg)
        100%
            transform: rotate(360deg)
    .intro
        position: absolute
        top: 55vh
        width: 30vw
        left: 25vw
        transform: translate(-50%)
    .corner
        position: absolute
        font-family: Michroma, Helvetica, sans-serif
        color: #fff
        font-size: 0.8rem
        letter-spacing: 1px
        z-index: 2
        &.left
            left: 1rem
        &.right
            right: 1rem
        &.top
            top: 1rem
        &.bottom
            bottom: 1rem
    .clip-rect
        z-index: 1
        position: absolute
        left: 50vw
        overflow: hidden
        width: 100vw
        height: 100vh
        background: rgba(15, 15, 15, 0.85)
        img
            opacity: 59%
            position: absolute
            left: 25vw
            top: 50vh
            margin-top: -65vh
            margin-left: -65vw
            width: 130vw
            height: 130vh
            animation: spin 120s linear infinite
    .text-box
        z-index: 4
        position: absolute
        left: 0
        top: 10vh
        width: 100vw
        .inner
            position: absolute
            left: calc(67vw - 15vh)
            width: 35vw
            height: 80vh
            opacity: 0
            transition: opacity 1.5s $cub-bez
            p
                margin-bottom: 0.6rem
        &.is-inview .inner
            opacity: 1

#modal
    background: black
    position: fixed
    height: 100vh
    width: 100vw
    top: 0
    left: 0
    z-index: 100
    transition: opacity 2s ease-in-out
    &.show
        display: block
    &.active
        opacity: 1
        transition-duration: 1s
    //&:empty
    display: none
    opacity: 0
    .close
        position: absolute
        top: 3em
        right: 3em
    &.fight h1
        position: absolute
        font-family: Michroma, Helvetica, sans-serif
        margin-bottom: 0
        margin-top: 5px
        text-align: left
        font-size: 130px
        line-height: 110px
        color: rgba(184, 54, 54, 0.527)
        text-transform: uppercase
        transition: opacity 5s ease-in-out
        .line
            opacity: 0
            transform-origin: center top
            transform-style: preserve-3d
            transform: translateY(100%) rotateX(-80deg)
            transition: opacity 0s $cub-bez, transform 0s $cub-bez
        &.is-inview .line
            transform: none
            opacity: 1
            transition-duration: .8s
            @for $i from 1 through 6
                &:nth-child(#{$i})
                    transition-delay: $i * .3s
    &.fear
        background-color: rgba(180,0,0,1)
        padding: 3em
        overflow: hidden
        h1.blend
            font-size: 7rem
            color: rgba(114, 0, 0, 1)
            margin: 0
        .para
            font-family: monospace
            margin: 2em 0
            max-width: 40em
        .anim
            position: absolute
            mix-blend-mode: multiply
            right: 20vw
            top: 35vh
    &.poems
        display: flex
        align-items: center
        justify-content: center
        .poem1
            position: relative
            width: 30vw
            min-width: 45em
            p:not(:first-child)
                margin-top: 0.75rem
        img
            position: absolute
            z-index: -1
    &.box
        canvas
            position: absolute
            transition: width 1.5s $cub-bez, height 1.5s $cub-bez, top 1.5s $cub-bez
        .close-icon
            width: 30px
            height: 30px
            position: absolute
            top: 1em
            right: 1em
            box-shadow: none
            opacity: 0
            transition: opacity 500ms ease-out
            cursor: pointer
            &.active
                opacity: 1
    &.noise
        padding: 15vmin
        #noise
            position: absolute
            z-index: -1
            top: 0
            left: 0
        *
            max-width: 48em
        h5
            font-family: Migra
            font-size: 2.5rem
        p.light
            margin-top: 2em
        p.dark
            margin-top: 2em
            color: #fff


.container.ghosts
    perspective: 500px
    perspective-origin: center center
    height: 250vh
    z-index: 3
    margin-top: -15vh
    display: inline-flex !important
    align-items: center
    justify-content: center
    .ghost
        position: absolute
        // widths are defined in-HTML to prevent race condition
        //width: 270px
        //&.prose
        //    width: 400px
        &.sym
            //width: 170px
            img
                pointer-events: none
            //mix-blend-mode: difference
        height: auto
        top: 100vh
        left: 50vw
        &.initialized
            transition: transform 1s ease-out, opacity 2s ease-out
        &.hover
            transition-duration: 0.5s
            z-index: 999 !important
            opacity: 1 !important
            .inner
                border-color: #9451ff
        &.sym .inner
            cursor: pointer
        .inner
            cursor: grab
            user-select: none
            width: 100%
            height: 100%
            margin-left: -50%
            margin-top: -50%
            box-sizing: border-box
            &.text
                padding: 1rem
                color: white
                border: 1px solid white
                background: black
                font-family: monospace
                line-height: 1.2rem

.container.ghosts .frame
    .head-obj
        position: absolute
        top: calc(50% - 150px)
        left: calc(50% - 150px)
        width: 300px
        height: 300px
    .symbols
        position: absolute
        transform: rotate3d(10, 10, 1, 42deg)
        transition: transform 0.5s $cub-bez
        &:hover
            transform: rotate3d(10, 10, 1, 15deg)
        .symbol
            cursor: grab
            position: absolute
            top: 50%
            left: 50%
            transition: width 0.3s $cub-bez, height 0.3s $cub-bez
            width: 60px
            height: 60px
            z-index: 1
            &:hover
                z-index: 2
                width: 100px
                height: 100px
            img
                width: 100%
                height: 100%
                margin-left: -50%
                margin-top: -50%
            .popup-box
                overflow: hidden
                position: absolute
                width: 0
                max-height: 0
                top: 0
                left: 0
                transition: width 0.3s $cub-bez, max-height 0.5s $cub-bez 0.3s, left 0.3s $cub-bez, top 0.5s $cub-bez 0.3s
                box-sizing: border-box
                .text-wrapper
                    position: relative
                    width: 40vw
                    box-sizing: border-box
            &:hover .popup-box
                outline: 3px solid #0017a0
                width: 40vw
                left: -20vw

